@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap);
* {
  box-sizing: border-box;
  font-family: "Libre Baskerville", serif;
}

.sidebar {
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  width: 13.2em;
  background-color: #f1f1f1;
  position: fixed;
  height: 100%;
  overflow: auto;
  background-color: #454ac3;
  text-align: left;
}
h4 {
  text-align: center;
  margin-top: 100px;
  color: #fefefe;
}
.sidebar label {
  display: block;
  color: black;
  padding: 14px;
  margin: 1rem;
  border-radius: 1em;
  background: white;
  border: 2px solid rgb(214, 212, 212);
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: bold;
  color: #454ac3;
}
.main {
  position: absolute;
  left: 210px;
  font-size: 28px;
  padding: 0px 10px;
}
.inputt {
  padding: 0.4rem;
  width: 95%;
  font-size: 20px;
  border: none;
  border-radius: 10px;
  box-shadow: 5px 8px 15px 5px rgba(0, 0, 0, 0.22);
}
::-webkit-input-placeholder {
  color: #454ac3;
  font-weight: 600;
}
::placeholder {
  color: #454ac3;
  font-weight: 600;
}

@media screen and (max-width: 500px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar label {
    float: left;
  }
  .main {
    left: 1em;
  }
  .list-card {
    left: 1em;
  }
  .inputt {
    width: 70%;
    margin-top: 5%;
    position: relative;
    left: 10%;
  }
}

.card-head {
  width: 6em;
  border-radius: 20px;
  height: auto;
  margin: 5% 2%;
  padding: 0%;
  box-sizing: border-box;
  border-radius: 6px;
  box-shadow: 5px 8px 15px 5px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.card-image {
  border-radius: 10px;
  padding: 3px;
  width: 163px;
  height: 120px;
}
.card-detail {
  font-size: 0.7em;
  font-weight: 700;
  text-align: center;
  color: #454ac3;
}
.list-card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

